import React, { useState, useEffect } from 'react';
import {
  Box,
  Button,
  TextField,
  Typography,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  CircularProgress,
  Alert,
  MenuItem,
  FormControl,
  InputLabel,
  Select,
} from '@mui/material';
import { styled } from '@mui/material/styles';

// Always use localhost for the browser since browser can't access Docker service names
const API_URL = 'http://localhost:4000';

const Container = styled(Box)({
  maxWidth: 1200,
  margin: '0 auto',
  padding: '24px',
});

const FormPaper = styled(Paper)({
  padding: '24px',
  marginBottom: '24px',
  borderRadius: '8px',
  backgroundColor: 'white',
  boxShadow: 'none',
  border: '1px solid #e0e0e0',
});

const StyledTextField = styled(TextField)({
  '& .MuiOutlinedInput-root': {
    borderRadius: '4px',
    backgroundColor: 'white',
  },
  '& .MuiOutlinedInput-input': {
    padding: '14px',
  },
});

const StyledFormControl = styled(FormControl)({
  '& .MuiOutlinedInput-root': {
    borderRadius: '4px',
    backgroundColor: 'white',
  },
  '& .MuiOutlinedInput-input': {
    padding: '14px',
  },
});

const PredictButton = styled(Button)({
  backgroundColor: '#0074d9',
  color: 'white',
  padding: '12px',
  textTransform: 'uppercase',
  fontWeight: 'bold',
  '&:hover': {
    backgroundColor: '#0063b1',
  },
  borderRadius: '4px',
});

const StyledTableHead = styled(TableHead)({
  '& th': {
    backgroundColor: '#0074d9',
    color: 'white',
    fontWeight: 'bold',
    padding: '12px 16px',
  }
});

const DynamicPricingForm = () => {
  const [formData, setFormData] = useState({
    godown_code: '',
    date: '2025-03-20',
  });

  const [godownCodes, setGodownCodes] = useState([]);
  const [godownsLoading, setGodownsLoading] = useState(false);
  const [predictions, setPredictions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Fetch godown codes on component mount
  useEffect(() => {
    fetchGodownCodes();
  }, []);

  const fetchGodownCodes = async () => {
    setGodownsLoading(true);
    try {
      const response = await fetch(`${API_URL}/api/godowns`);
      if (!response.ok) {
        throw new Error(`Failed to fetch godown codes: ${response.statusText}`);
      }
      const data = await response.json();
      setGodownCodes(data || []);
      
      // Set the first godown code as default if available
      if (data && data.length > 0) {
        setFormData(prev => ({
          ...prev,
          godown_code: data[0].code
        }));
      }
    } catch (err) {
      console.error('Error fetching godown codes:', err);
      setError(`Failed to fetch godown codes: ${err.message}`);
    } finally {
      setGodownsLoading(false);
    }
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    setError(null);
  };

  const formatDate = (dateStr) => {
    try {
      const [year, month, day] = dateStr.split('-');
      return `${day}-${month}-${year}`;
    } catch (err) {
      console.error('Date formatting error:', err);
      return dateStr;
    }
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    const requestData = {
      godown_code: formData.godown_code.trim(),
      date: formData.date.split('-').reverse().join('-') // Convert to DD-MM-YYYY
    };

    console.log('Making prediction request:', {
      url: `${API_URL}/api/predict`,
      data: requestData
    });

    try {
      const response = await fetch(`${API_URL}/api/predict`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json',
        },
        body: JSON.stringify(requestData),
      });

      console.log('Response received:', {
        status: response.status,
        statusText: response.statusText,
        headers: Object.fromEntries(response.headers.entries())
      });

      if (!response.ok) {
        const errorText = await response.text();
        console.error('Error response:', errorText);
        throw new Error(`Server error: ${response.status} - ${errorText || response.statusText}`);
      }

      const data = await response.json();
      console.log('Prediction data:', data);
      setPredictions(Array.isArray(data) ? data : []);
    } catch (err) {
      console.error('Request failed:', err);
      setError(err.message || 'Failed to fetch predictions');
    } finally {
      setLoading(false);
    }
  };

  return (
    <Container>
      <Typography variant="h4" gutterBottom style={{ fontWeight: 'normal' }}>
        Dynamic Price Predictor
      </Typography>

      <FormPaper>
        <form onSubmit={handleSubmit}>
          <Box mb={3}>
            <Typography component="label" style={{ display: 'block', marginBottom: '8px' }}>
              Godown Code *
            </Typography>
            <StyledFormControl fullWidth variant="outlined">
              {godownsLoading ? (
                <CircularProgress size={24} sx={{ margin: '14px auto' }} />
              ) : (
                <Select
                  name="godown_code"
                  value={formData.godown_code}
                  onChange={handleChange}
                  displayEmpty
                  inputProps={{
                    style: { 
                      backgroundColor: 'white',
                      fontSize: '14px'
                    }
                  }}
                >
                  <MenuItem value="" disabled>
                    Select a godown
                  </MenuItem>
                  {godownCodes.map((option) => (
                    <MenuItem key={option.code} value={option.code}>
                      {option.code} ({option.count} items)
                    </MenuItem>
                  ))}
                </Select>
              )}
            </StyledFormControl>
          </Box>

          <Box mb={3}>
            <Typography component="label" style={{ display: 'block', marginBottom: '8px' }}>
              Date *
            </Typography>
            <StyledTextField
              fullWidth
              type="date"
              name="date"
              value={formData.date}
              onChange={handleChange}
              variant="outlined"
              inputProps={{
                style: { 
                  backgroundColor: 'white',
                  fontSize: '14px'
                }
              }}
            />
          </Box>

          <Box mt={4}>
            <PredictButton
              type="submit"
              variant="contained"
              fullWidth
              disabled={loading || !formData.godown_code}
            >
              {loading ? <CircularProgress size={24} color="inherit" /> : 'PREDICT PRICES'}
            </PredictButton>
          </Box>
        </form>
      </FormPaper>

      {error && (
        <Alert severity="error" sx={{ mb: 3 }}>
          {error}
        </Alert>
      )}

      {predictions.length > 0 && (
        <TableContainer component={Paper}>
          <Table>
            <StyledTableHead>
              <TableRow>
                <TableCell>Item No</TableCell>
                <TableCell>Previous Enter Rate</TableCell>
                <TableCell>Predicted Retail Rate</TableCell>
                <TableCell>Adjusted Price</TableCell>
                <TableCell>Cost Rate</TableCell>
                <TableCell>MRP</TableCell>
              </TableRow>
            </StyledTableHead>
            <TableBody>
              {predictions.map((item, index) => (
                <TableRow key={index}>
                  <TableCell>{item.item_no}</TableCell>
                  <TableCell>{item.previous_enter_rate?.toFixed(2) || 'N/A'}</TableCell>
                  <TableCell>{item.predicted_retail_rate?.toFixed(2) || 'N/A'}</TableCell>
                  <TableCell>{item.adjusted_price?.toFixed(2) || 'N/A'}</TableCell>
                  <TableCell>{item.cost_rate?.toFixed(2) || 'N/A'}</TableCell>
                  <TableCell>{item.mrp?.toFixed(2) || 'N/A'}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Container>
  );
};

export default DynamicPricingForm;